@tailwind base;
@tailwind components;
@tailwind utilities;

* {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html {
	@apply bg-background;
}

.orientation svg {
	@apply fill-gray-200;
	margin-bottom: 20px;
}

.bi-phone {
	animation: rotation 3s infinite ease-in-out;
}

@keyframes rotation {
	0% {
		transform: rotate(0deg);
	}
	20% {
		transform: rotate(90deg);
	}
	40% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(0deg);
	}
}

/* home */

.keyword {
	@apply text-center font-semibold text-white text-opacity-10;
}

/* tg cloud */

.tagcloud {
	@apply font-mo;
	font-size: 20px;
	margin: auto;
	width: 50%;
}

.tagcloud--item {
	white-space: nowrap;
}

.tagcloud--item:hover {
	cursor: default;
	color: #36454f;
	transition: all 0.3s;
}

/* custom scroll bar */

.y-scroll::-webkit-scrollbar {
	width: 15px;
}

.y-scroll::-webkit-scrollbar-track {
	background: transparent;
}

.y-scroll::-webkit-scrollbar-thumb {
	@apply bg-gray-200 hover:bg-gray-300 dark:bg-gray-700 dark:hover:bg-gray-600;
	border-radius: 20px;
	background-clip: padding-box;
	border: 5px solid rgba(0, 0, 0, 0);
}

.y-scroll::-webkit-scrollbar-thumb:hover {
	background-clip: padding-box;
	border: 5px solid rgba(0, 0, 0, 0);
}
